*{
    text-decoration: none;
}


.Item{
    max-width: 380px;
    height: 350px;
    overflow: hidden;
    border-radius: 20px;
    margin-bottom: 50px;
    color: black;
    text-decoration: none;
}



.item__img img{
    width: 100%;
    border-radius: 20px;
    transition: all .7s ;
   
}


.Item:hover img{
    transform: scale(1.1);
    cursor: pointer;
    border-radius: 40px;
}




.item__img {
    border-radius: 40px;
    overflow: hidden;
    text-decoration: none;
}



.item__title{
    font-size: 22px;
    color: black;
    text-decoration: none;
    padding-left: 10px;
    transition: all 0.4s;
}



.item__title:hover{
    cursor: pointer;
    color: #fb9200dc;
}


@media(max-width: 500px){
    .Item{
        margin-bottom: 10px;
        height: 320px;
        width: 96%;
        margin: 0 auto;
    }



    .Item:hover img{
        border-radius: 10px;
        transform: scale(1);
    }
}