.wrapper {
    height: 79.5vh;
}

.item {
    width: 1200px;
    margin: 0 auto;
    padding: 40px;
    background: rgb(42, 0, 156);
    margin-top: 30px;
    border-radius: 20px;
    
    transition: 0.5s !important;
    height: 300px;
    overflow: hidden;
}

.inner__item{
    display: flex;
    justify-content: space-between;
    
}

.title {
    font-size: 30px;
    font-weight: 600;
    padding-bottom: 30px;
    color: white;
}

.text {
    width: 70%;
    padding-bottom: 40px;
    color: #a7a7a7;
}

.link div {
    
    padding: 15px 10px;
    padding-bottom: 20px;
    transition: all 0.3s;
    display: flex;
}

.link {
    color: black;
    text-decoration: none;
    font-size: 22px;
    font-weight: 600;
    border-radius: 10px;
    transition: all 0.3s;
    display: flex;
    width: 140px;
    background: white;
    margin-bottom: 100px;
    
}


.link:hover {
    transform: scale(1.03);
}

.item img {
    border-radius: 15px;
}

.activeProduct {
    height: 700px;
}


.link.active img{
    transform: rotate(270deg);
}


.activeProduct .link{
    margin-bottom: 40px;
}


.dropdown{
    color: #a7a7a7;
}