.wrapper{
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    width: 100%;
    justify-content: space-between;
    border-bottom: 1px solid rgb(161, 161, 161);
    padding: 20px;
    color: black;
    
}



.title{
    font-size: 22px;
    font-weight: 600;
    transition: all 0.3s;
}


.place{
    color: grey;
}



.title:hover{
    cursor: pointer;
    color: #fb9200dc;
}



.link__wrap{
    cursor: inherit;
}