    }



    .header_header__burger__1u9nq::after, .header_header__burger__1u9nq::before{
        transition: all 0.5s;
        content: '';
        position: absolute;
        left: 0;
        width: 40px;
        height: 3px;
        background-color: #fb9200dc;
    }


    .header_header__burger__1u9nq::after{
        top: 10px;
    }
    .header_header__burger__1u9nq::before{
        top: -15px;
    }


    .header_header__burger__1u9nq span{
        opacity: 1;
        transition: all 0.5s;
        position: absolute;
        left: 0;
        top: -3px;
        width: 40px;
        height: 3px;
        background-color: #fb9200dc;
    }


    .header_mobile__menu__Jq0nk{
        height: 100vh;
        width: 70%;
        background: white;
        display: block;
        position: fixed;
        top: 100px;
        transition: all 0.5s;
        right: -100%;
