    }


    .header_header__34iZ5{
        position: fixed;
    }



    .header_header__burger__1u9nq.header_active__1WAHy::after{
        transform: rotate(315deg);
        top: -5px;
    }


    .header_header__burger__1u9nq.header_active__1WAHy::before{
        transform: rotate(45deg);
        top: -4px;
    }

    .header_header__burger__1u9nq.header_active__1WAHy span{
        opacity: 0;
    }




    .header_mobile__menu__Jq0nk.header_active__1WAHy{
        right: 0;
    }
}

