* {
  box-sizing: border-box;
}

.animation {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  z-index: 999;
  left: 0;
  animation-name: animationEnd;
  animation-duration: 12s;
  animation-delay: 0s;
  animation-fill-mode: forwards;
}

.animation video {
  transition: all 1s;
}

@keyframes animationStart {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes animationEnd {
  0% {
  }
  100% {
    z-index: -1;
  }
}

.animation__video {
  width: 100%;
  animation-name: animationStart;
  animation-duration: 2s;
  animation-delay: 10s;
  animation-fill-mode: forwards;
}
