.FilmPage{
    max-width: 1200px;
    margin: 0 auto;
}


.wrapper__buy{
    display: flex;
    width: 100%;
    justify-content: flex-end;
    margin-top: 30px;
}


h3{
    margin-bottom: 30px;
}

iframe{
    margin-bottom: 30px;
}


.information__film{
    font-size: 18px;
    font-weight: 500;
}




.wrapper__buy  a{
    color: white;
    font-size: 22px;
    font-weight: 600;
    padding: 15px 30px;
    text-decoration: none;
    border-radius: 10px;
    background-color: black;
    transition: all 0.5s;
    padding-bottom: 20px;
}


.wrapper__buy  a:hover{
    transform: scale(1.04);
    background-color: #fb9200dc;
}



@media(max-width: 500px){
    iframe{
        width: 97% !important;
        height: 230px !important;
        margin: 0 auto;
        display: block;
    }
}
